import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';

import { increment, decrement } from "../../../app/store/counterSlice";
import dots from '../../../app/hooks/forLoading/dots'
import { selectBebidas } from '../../../app/store/pizzasSlice';
import { Portada } from '../secciones/Portada';
import { imgArr} from './imgBebidasArr';

import './bebidas.css'

function Bebidas() {

  const pizzasStatus = useSelector(state => state.pizzas.status);
  const error = useSelector(state => state.pizzas.error);
  const pizzas = useSelector(selectBebidas);
  const allCounter = useSelector(state => state.counter.bebidas);

  const [showCarrito, setShowCarrito] = useState(null);

  let lengthOfArr; 

  const dispatch = useDispatch();

  let renderPizza = [];
  let renderPortada;
  if (pizzasStatus === 'loading') {
    renderPizza = (
      <section className='loading'>
        <h2>Cargando Bebidas</h2>
        {dots()}
      </section>
    )
  } else if (pizzasStatus === 'succeeded') {

    lengthOfArr = pizzas.arrBebidas.length;

    renderPortada = <Portada pizzaTitle={pizzas.name} />;

    // You are displaying all complementos here
    for (let i=0; i < lengthOfArr; i++ ) {
      renderPizza.push(
        <section className={`${pizzas.arrBebidas[i].name} ${pizzas.arrBebidas[i].description}`} key={i} >
          <figure className='imgPizza'><img src={imgArr[i]} alt={`${pizzas.arrBebidas[i].name}`} /></figure>
          <h2>{pizzas.arrBebidas[i].name}</h2>
          <p className='size'>{pizzas.arrBebidas[i].description}</p>
          <section className='details'>
            <p className='precio'>S/. {pizzas.arrBebidas[i].price}</p>
            <button className='mas' onClick={() => dispatch(increment({name: 'bebidas', id: pizzas.arrBebidas[i].id}))}>+</button>
            <button className='menos' onClick={() => dispatch(decrement({name: 'bebidas', id: pizzas.arrBebidas[i].id}))}>-</button>
            <p className='counter' style={{color: !allCounter[i+1] && 'gainsboro', fontWeight: !allCounter[i+1] && 600}}>
              ( {allCounter[i+1]} )
            </p>
          </section>
        </section>
      );  
    }

  } else if (pizzasStatus === 'failed') {
    renderPizza = <section>{error}</section>
  }

  // This part is for rendering .botonCarrito if allCounter[i] > 0;
  // d is the last id of complementos or the number of complementos
  useEffect(() => {
    const arrNumPizzas = [];
    for ( let i=1; i <= lengthOfArr; i++) {
      allCounter[i] > 0 && arrNumPizzas.push(allCounter[i]);
    }
    if (arrNumPizzas.length !== 0) {
      setShowCarrito(
        <div><Link to='/carrito'><button className='botonCarrito'>Ir al carrito</button></Link></div>
      );
    } else {
      setShowCarrito(null);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [allCounter])

  return (
    <React.Fragment>
      {renderPortada}
      <section className='bebidas'>
        {pizzasStatus === 'loading' && renderPizza}
        {pizzasStatus === 'succeeded' && (
          <section className='bebidasInside'>
            {renderPizza}
          </section>
        )}
        {pizzasStatus === 'failed' && renderPizza}
        {showCarrito}
      </section>
    </React.Fragment>
  )
}

export default Bebidas
