import React, { useState, useEffect } from 'react';
import { useSelector} from 'react-redux';
import { Link } from 'react-router-dom';

import DisplayPizzas from '../../../app/hooks/forPizzas/DisplayPizzas'
import dots from '../../../app/hooks/forLoading/dots'
import { selectTradicionalesPizzas } from '../../../app/store/pizzasSlice';
import { Portada } from '../secciones/Portada';
import { imgArr} from './imgTradicionalesArr';

import './tradicionales.css';

export const Tradicionales = () => {
  
  const pizzasStatus = useSelector(state => state.pizzas.status);
  const error = useSelector(state => state.pizzas.error);
  // pizzas = {name: 'De la Casa', arrPizzas: [{...}, {...}, ...]}
  const pizzas = useSelector(selectTradicionalesPizzas);
  const allCounter = useSelector(state => state.counter.tradicionales);
  
  let lengthOfArr; 

  const [showCarrito, setShowCarrito] = useState(null);

  let renderPizza = [];
  let renderPortada;
  if (pizzasStatus === 'loading') {
    renderPizza = (
      <section className='loading'>
        <h2>Cargando Pizzas</h2>
        {dots()}
      </section>
    )
  } else if (pizzasStatus === 'succeeded') {

    lengthOfArr = pizzas.arrPizzas.length;
    
    renderPortada = <Portada pizzaTitle={pizzas.name} />;

    // You are displaying all Tradicionales here, from a component
    for (let i=0; i < lengthOfArr; i += 3) {
      renderPizza.push(
        <DisplayPizzas 
          key={i}
          pizzas={pizzas}
          imgArr={imgArr}
          allCounter={allCounter}
          i={i}
          size='size_tradicional'
          dispatchName='tradicionales'
        />
      );  
    }

  } else if (pizzasStatus === 'failed') {
    renderPizza = <section>{error}</section>
  }

  // This part is for rendering .botonCarrito if allCounter[i] > 0;
  // d is the last id of deLaCasa pizzas or the number of pizzas
  useEffect(() => {
    const arrNumPizzas = [];
    for ( let i=1; i <= lengthOfArr; i++) {
      allCounter[i] > 0 && arrNumPizzas.push(allCounter[i]);
    }
    if (arrNumPizzas.length > 0) {
      setShowCarrito(
        <div><Link to='/carrito'><button className='botonCarrito'>Ir al carrito</button></Link></div>
      );
    } else {
      setShowCarrito(null);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [allCounter])

  return (
    <React.Fragment>
      {/* renderPortada is a component and every style is specify in its component */}
      {renderPortada}
      <section className='tradicionales'>
      {pizzasStatus === 'loading' && renderPizza}
        {pizzasStatus === 'succeeded' && (
          <section className='tradicionalesInside'>
            {renderPizza}
          </section>
        )}
        {pizzasStatus === 'failed' && renderPizza}
        {showCarrito}
      </section>
    </React.Fragment>
  );
};