import { createSlice } from '@reduxjs/toolkit'

const initialState = {
  totalPrice: null,
  productos: null,
  nombres: null,
  apellidos: null,
  email: null,
  entrega: null,
  distrito: null,
  urbanizacion: null,
  telefono: null,
  calle: null,
  numero: null,
  apartamento: null,
  bloque: null,
  detalles: null,
};

const infoUser = createSlice({
  name: 'infoUser',
  initialState,
  reducers: {
    // The issue (IMMER) is the use of an arrow function with no curly braces as the reducer,
    // because that acts as an implicit return statement.
    // Solution: void(...) OR {...}
    updateTotalPrice: (state, action) => void(state.totalPrice = action.payload),
    updateProductos: (state, action) => void(state.productos = action.payload),
    updateNombres: (state, action) => void(state.nombres = action.payload),
    updateApellidos: (state, action) => void(state.apellidos = action.payload),
    updateEmail: (state, action) => void(state.email = action.payload),
    updateEntrega: (state, action) => void(state.entrega = action.payload),
    updateDistrito: (state, action) => void(state.distrito = action.payload),
    updateUrbanizacion: (state, action) => void(state.urbanizacion = action.payload),
    updateTelefono: (state, action) => void(state.telefono = action.payload),
    updateCalle: (state, action) => void(state.calle = action.payload),
    updateNumero: (state, action) => void(state.numero = action.payload),
    updateApartamento: (state, action) => void(state.apartamento = action.payload),
    updateBloque: (state, action) => void(state.bloque = action.payload),
    updateDetalles: (state, action) => void(state.detalles = action.payload),
  },
});

export default infoUser.reducer;

export const {
  updateTotalPrice,
  updateProductos,
  updateNombres,
  updateApellidos,
  updateEmail,
  updateEntrega,
  updateDistrito,
  updateUrbanizacion,
  updateTelefono,
  updateCalle,
  updateNumero,
  updateApartamento,
  updateBloque,
  updateDetalles,
} = infoUser.actions;