import { useHistory } from "react-router-dom";
import './portada.css'

import divisor from '../../../images/divisor.png';
export const Portada = () => {

  const history = useHistory();

  return (
    <section className='inicio'>
      <figure className='divisor'><img src={divisor} alt='divisor' /></figure>
      <h2>Lo mejor en Pizzas</h2>
      <h1>Pizzarela Artesanal</h1>
      <p>
        Pizzas Artesanales con ingredientes selectos,
        hechas en horno a la piedra 🍕
      </p>
      <div className='divButton'>
        <div><button onClick={() => {history.push('/De la Casa')}}>Pizzas de la Casa</button></div>
        <div><button onClick={() => {history.push('/Tradicionales')}}>Pizzas Tradicionales</button></div>
      </div>
    </section>
  );
};