import './portada.css'

import divisor from '../../images/divisor.png';
export const Portada = () => {
  return (
    <section className='portadaContactanos'>
      <figure className='divisor'><img src={divisor} alt='divisor' /></figure>
      <h1>Contáctanos</h1>
      <p>
      También puedes ordenar tu pizza en cualquiera de nuestras redes sociales Facebook,
      Instagram y Whatsapp o llamarnos directamente y preguntar por más información
      </p>
    </section>
  );
};