import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Formik, Form, Field, ErrorMessage} from 'formik'
import cloneDeep from 'lodash.clonedeep'
import * as Yup from 'yup'

import { pushPromo, popPromo } from "../../store/counterSlice";

function DisplayPromo({ i, promoName, promoOptions, promoPrice, promoImg }) {

  const promosCounter = useSelector(state => state.counter.promos)
  
  const dispatch = useDispatch();

  let renderForm = [];
  let renderPizzaOption = [];
  let renderBebidaOption = [];
  let initialValues;
  let validationSchema;
  let info;

  // For render
  if (promoName === 'Pack Familiar 1') {

    initialValues = {
      'Pack Familiar 1 pizza': '',
      'Pack Familiar 1 bebida': '',
    }
    validationSchema = Yup.object({
      'Pack Familiar 1 pizza': Yup.string().required('Elija una pizza'),
      'Pack Familiar 1 bebida': Yup.string().required('Elija una bebida')
    })

    renderPizzaOption = promoOptions.pizzas.map((element, index) => {
      return (
        <option key={`${element.id} ${element.name}`} value={index}>{element.name} {element.size_tradicional}</option>
      )
    })
    renderBebidaOption = promoOptions.bebidas.map((element, index) => {
      return(
        <option key={`${element.id} ${element.name}`} value={index}>{element.name} {element.description}</option>
      )
    })

    renderForm = (
      <React.Fragment>
        <section className='pizza'>
          <Field className='inputPizza' as='select' name={`${promoName} pizza`}>
            <option value=''>Elija pizza</option>
            {renderPizzaOption}
          </Field>
          <ErrorMessage name={`${promoName} pizza`} component='div' />
        </section>
        <section className='bebida'>
          <Field className='inputBebida' as='select' name={`${promoName} bebida`}>
            <option value=''>Elija bebida</option>
            {renderBebidaOption}
          </Field>
          <ErrorMessage name={`${promoName} bebida`} component='div' />
        </section>
      </React.Fragment>
    )

  } else if (promoName === 'Pack Familiar 2') {

    initialValues = {
      'Pack Familiar 2 pizza': '',
      'Pack Familiar 2 bebida': '',
    }
    validationSchema = Yup.object({
      'Pack Familiar 2 pizza': Yup.string().required('Elija una pizza'),
      'Pack Familiar 2 bebida': Yup.string().required('Elija una bebida')
    })

    renderPizzaOption = promoOptions.pizzas.map((element, index) => {
      return (
        <option key={`${element.id} ${element.name}`} value={index}>{element.name} {element.size_casa}</option>
      )
    })
    renderBebidaOption = promoOptions.bebidas.map((element, index) => {
      return(
        <option key={`${element.id} ${element.name}`} value={index}>{element.name} {element.description}</option>
      )
    })

    renderForm = (
      <React.Fragment>
        <section className='pizza'>
          <Field className='inputPizza' as='select' name={`${promoName} pizza`}>
            <option value=''>Elija pizza</option>
            {renderPizzaOption}
          </Field>
          <ErrorMessage name={`${promoName} pizza`} component='div' />
        </section>
        <section className='bebida'>
          <Field className='inputBebida' as='select' name={`${promoName} bebida`}>
            <option value=''>Elija bebida</option>
            {renderBebidaOption}
          </Field>
          <ErrorMessage name={`${promoName} bebida`} component='div' />
        </section>
      </React.Fragment>
    )

  } else if (promoName === 'Promo para Compartir') {

    initialValues = {
      'Promo para Compartir pizza': '',
      'Promo para Compartir bebida': '',
    }
    validationSchema = Yup.object({
      'Promo para Compartir pizza': Yup.string().required('Elija una pizza'),
      'Promo para Compartir bebida': Yup.string().required('Elija una bebida')
    })

    renderPizzaOption = promoOptions.pizzas.map((element, index) => {
      return (
        <option key={`${element.id} ${element.name}`} value={index}>{element.name} {element.size_tradicional}</option>
      )
    })
    renderBebidaOption = promoOptions.bebidas.map((element, index) => {
      return(
        <option key={`${element.id} ${element.name}`} value={index}>{element.name} {element.description}</option>
      )
    })

    renderForm = (
      <React.Fragment>
        <section className='pizza'>
          <Field className='inputPizza' as='select' name={`${promoName} pizza`}>
            <option value=''>Elija pizza</option>
            {renderPizzaOption}
          </Field>
          <ErrorMessage name={`${promoName} pizza`} component='div' />
        </section>
        <section className='bebida'>
          <Field className='inputBebida' as='select' name={`${promoName} bebida`}>
            <option value=''>Elija bebida</option>
            {renderBebidaOption}
          </Field>
          <ErrorMessage name={`${promoName} bebida`} component='div' />
        </section>
      </React.Fragment>
    )

  } else if (promoName === 'Dúos Grandes') {

    initialValues = {
      'Dúos Grandes pizza1': '',
      'Dúos Grandes pizza2': '',
    }
    validationSchema = Yup.object({
      'Dúos Grandes pizza1': Yup.string().required('Elija una pizza'),
      'Dúos Grandes pizza2': Yup.string().required('Elija una pizza')
    })

    renderPizzaOption = promoOptions.pizzas.map((element, index) => {
      return (
        <option key={`${element.id} ${element.name}`} value={index}>{element.name} {element.size_tradicional}</option>
      )
    })

    renderForm = (
      <React.Fragment>
        <section className='pizza'>
          <Field className='inputPizza' as='select' name={`${promoName} pizza1`}>
            <option value=''>Elija primera pizza</option>
            {renderPizzaOption}
          </Field>
          <ErrorMessage name={`${promoName} pizza1`} component='div' />
        </section>
        <section className='pizza'>
          <Field className='inputPizza' as='select' name={`${promoName} pizza2`}>
            <option value=''>Elija segunda pizza</option>
            {renderPizzaOption}
          </Field>
          <ErrorMessage name={`${promoName} pizza2`} component='div' />
        </section>
      </React.Fragment>
    )

  } else if (promoName === 'Promo Entre Patas') {

    initialValues = {
      'Promo Entre Patas pizza': '',
    }
    validationSchema = Yup.object({
      'Promo Entre Patas pizza': Yup.string().required('Elija una pizza'),
    })

    renderPizzaOption = promoOptions.pizzas.map((element, index) => {
      return (
        <option key={`${element.id} ${element.name}`} value={index}>{element.name} {element.size_tradicional}</option>
      )
    })

    renderForm = (
      <React.Fragment>
        <section className='pizza'>
          <Field className='inputPizza' as='select' name={`${promoName} pizza`}>
            <option value=''>Elija pizzza</option>
            {renderPizzaOption}
          </Field>
          <ErrorMessage name={`${promoName} pizza`} component='div' />
        </section>
      </React.Fragment>
    )

  }

  // For Redux
  const onSubmit = values => {
    if (promoName === 'Pack Familiar 1') {

      info = {
        id: i+1,
        grupo: 'Pack Familiar 1',
        precio: promoPrice, 
        pizza: promoOptions.pizzas[values['Pack Familiar 1 pizza']],
        complemento: promoOptions.complementos[0],
        bebida: promoOptions.bebidas[values['Pack Familiar 1 bebida']],
      }
      info = cloneDeep(info)
      info.pizza.cantidad = 1;
      info.bebida.cantidad = 1;
      info.complemento.cantidad = 1;
      dispatch(pushPromo({id: i+1, info: info}))
  
    } else if (promoName === 'Pack Familiar 2') {
  
      info = {
        id: i+1,
        grupo: 'Pack Familiar 2', 
        precio: promoPrice, 
        pizza: promoOptions.pizzas[values['Pack Familiar 2 pizza']],
        complemento: promoOptions.complementos[0], 
        bebida: promoOptions.bebidas[values['Pack Familiar 2 bebida']],
      }
      info = cloneDeep(info)
      info.pizza.cantidad = 1;
      info.bebida.cantidad = 1;
      info.complemento.cantidad = 1;
      dispatch(pushPromo({id: i+1, info: info}))
  
    } else if (promoName === 'Promo para Compartir') {
  
      info = {
        id: i+1,
        grupo: 'Promo para Compartir', 
        precio: promoPrice, 
        pizza: promoOptions.pizzas[values['Promo para Compartir pizza']],
        complemento: promoOptions.complementos[0],
        bebida: promoOptions.bebidas[values['Promo para Compartir bebida']],
      }
      info = cloneDeep(info)
      info.pizza.cantidad = 1;
      info.bebida.cantidad = 1;
      info.complemento.cantidad = 1;
      dispatch(pushPromo({id: i+1, info: info}))
  
    } else if (promoName === 'Dúos Grandes') {
  
      info = {
        id: i+1,
        grupo: 'Dúos Grandes', 
        precio: promoPrice, 
        pizza1: promoOptions.pizzas[values['Dúos Grandes pizza1']],
        pizza2: promoOptions.pizzas[values['Dúos Grandes pizza2']],
      }
      info = cloneDeep(info)
      info.pizza1.cantidad = 1;
      info.pizza2.cantidad = 1;
      dispatch(pushPromo({id: i+1, info: info}))
  
    } else if (promoName === 'Promo Entre Patas') {
  
      info = {
        id: i+1,
        grupo: 'Promo Entre Patas', 
        precio: promoPrice, 
        pizza: promoOptions.pizzas[values['Promo Entre Patas pizza']],
        complemento: promoOptions.complementos[0],
        bebida: promoOptions.bebidas[0],
      }
      info = cloneDeep(info)
      info.pizza.cantidad = 1;
      info.bebida.cantidad = 2;
      info.complemento.cantidad = 1;
      dispatch(pushPromo({id: i+1, info: info}))
  
    }
  }

  return (
    <section className={promoName}>
      <figure className='imgPromo'><img src={promoImg} alt={promoName} /></figure>
      <h2>{promoName}</h2>
      <Formik
        initialValues = { initialValues }
        validationSchema = { validationSchema }
        onSubmit = { onSubmit }
      >
        <Form>
          {renderForm}
          <section className='details'>
            <p className='precio'>S/. {promoPrice}</p>
            <button className='mas' type='submit'>+</button>
            <div className='menos' onClick={() => dispatch(popPromo({ id: i + 1 }))}><span>-</span></div>
            <p className='counter' style={{color: !promosCounter[i+1].length && 'gainsboro', fontWeight: !promosCounter[i+1].length && 600}}>
              ( {promosCounter[i+1].length} )
            </p>
          </section>
        </Form>
      </Formik>
    </section>
  )
}

export default DisplayPromo
