
const useDisplayCarrito = (x, nombreCounter, render, imgObj, imgName, nombrePizzas, size, toSum, displayedName, products) => {
  for ( let i=1; i <= x; i++) {
    // <DisplayCarrito imgObj={imgObj} imgName='deLaCasa' i={i} nombrePizzas={deLaCasaPizzas} size={size_casa} nombreCounter={deLaCasaCounter} />
    let pizzaObj = (nombreCounter[i] > 0) ? (nombrePizzas.find(index => index.id === i)) : null;
    nombreCounter[i] > 0 && render.push(
      // <DisplayCarrito
        // You are creating a unique key with id, name and size of each arr of Pizzas
        // since almost everything is the same in your array
      <section className='items' key={`${nombrePizzas[i-1].id}${nombrePizzas[i-1].name}${nombrePizzas[i-1][size]}`}>
        <figure className='imgPizza'><img src={imgObj[imgName][i]} alt={`${pizzaObj.name}`} /></figure>
        {/* "&nbsp;" is just a space */}
        <p className='pizza'>
          {/* You do not need <span> but probably you will refactor css */}
          <span>{pizzaObj.name}&nbsp;</span>
          <span className='size'>{pizzaObj[size]}</span>
        </p>
        <p className='cantidad'>{nombreCounter[i]}</p>
        <p className='precio'>S/. {pizzaObj.price}</p>
        <p className='precioCantidad'>S/. {(pizzaObj.price * nombreCounter[i]).toFixed(2)}</p>
      </section>
    );
    
    nombreCounter[i] > 0 && toSum.push(Number((pizzaObj.price * nombreCounter[i]).toFixed(2)));
    nombreCounter[i] > 0 && products.push({
      promo: false,
      grupo: displayedName,
      id: pizzaObj.id,
      nombre: pizzaObj.name,
      tamaño: pizzaObj[size],
      precio: Number(pizzaObj.price),
      cantidad: nombreCounter[i],
      subTotal: (pizzaObj.price * nombreCounter[i]).toFixed(2),
      imagen: imgObj[imgName][i],
    })
  }
  return [render, toSum];
};

export default useDisplayCarrito
