import React from 'react'
import { useDispatch } from 'react-redux';
import { increment, decrement } from '../../store/counterSlice';

function DisplayPizzas({ pizzas, imgArr, allCounter, i, size, dispatchName}) {

  const dispatch = useDispatch();

  return (
    <section className={`${pizzas.arrPizzas[i].name}`}>
          <figure className='imgPizza'><img src={imgArr[i]} alt={`${pizzas.arrPizzas[i].name}`} /></figure>
          <h2>{pizzas.arrPizzas[i].name}</h2>
          <section className='details'>
            <p className='precio'>S/. {pizzas.arrPizzas[i].price}</p>
            <p className='hyphen'>-</p>
            <p className='size'>{pizzas.arrPizzas[i][size]}</p>
            <button className='mas' onClick={() => dispatch(increment({name: dispatchName, id: pizzas.arrPizzas[i].id}))}>+</button>
            <button className='menos' onClick={() => dispatch(decrement({name: dispatchName, id: pizzas.arrPizzas[i].id}))}>-</button>
            <p className='counter' style={{color: !allCounter[i+1] && 'gainsboro', fontWeight: !allCounter[i+1] && 600}}>
              ( {allCounter[i+1]} )
            </p>
          </section>
          <section className='details'>
            <p className='precio'>S/. {pizzas.arrPizzas[i+1].price}</p>
            <p className='hyphen'>-</p>
            <p className='size'>{pizzas.arrPizzas[i+1][size]}</p>
            <button className='mas' onClick={() => dispatch(increment({name: dispatchName, id: pizzas.arrPizzas[i+1].id}))}>+</button>
            <button className='menos' onClick={() => dispatch(decrement({name: dispatchName, id: pizzas.arrPizzas[i+1].id}))}>-</button>
            <p className='counter' style={{color: !allCounter[i+2] && 'gainsboro', fontWeight: !allCounter[i+2] && 600}}>
              ( {allCounter[i+2]} )
            </p>
          </section>
          <section className='details'>
            <p className='precio'>S/. {pizzas.arrPizzas[i+2].price}</p>
            <p className='hyphen'>-</p>
            <p className='size'>{pizzas.arrPizzas[i+2][size]}</p>
            <button className='mas' onClick={() => dispatch(increment({name: dispatchName, id: pizzas.arrPizzas[i+2].id}))}>+</button>
            <button className='menos' onClick={() => dispatch(decrement({name: dispatchName, id: pizzas.arrPizzas[i+2].id}))}>-</button>
            <p className='counter' style={{color: !allCounter[i+3] && 'gainsboro', fontWeight: !allCounter[i+3] && 600}}>
              ( {allCounter[i+3]} )
            </p>
          </section>
          <p className='description'>{pizzas.arrPizzas[i].description}</p>
        </section>
  )
}

export default DisplayPizzas
