import React from 'react';
import { useSelector } from "react-redux";
import { Link } from 'react-router-dom';

import './answer.css';

export function Answer() {

  const answer = useSelector(state => state.clientAnswer);
  const status = answer.value.orderStatus;
  // const status = 'PAID';
  const infoUser = useSelector(state => state.infoUser);
//   const infoUser = {
//     totalPrice: "5280",
//     productos: [
//         {
//             grupo: "De la Casa",
//             id: 7,
//             nombre: "Lomazo",
//             tamaño: "Mediana",
//             precio: 23.90,
//             cantidad: 2,
//             subTotal: "47.80",
//             imagen: "sdf/s43.sdjp.jpg()"
//         }
//     ],
//     nombres: "Juan",
//     apellidos: "Estrada",
//     email: "juan_estrada_1@hotmail.com",
//     entrega: "local",
//     distrito: null,
//     urbanizacion: null,
//     telefono: null,
//     calle: null,
//     detalles: null
// }

  let productos = (status === 'PAID') ? 
  (
    // eslint-disable-next-line array-callback-return
    infoUser.productos.map((element, index) => {
      if (element.promo === false) {
        return (
          <section className='items' key={index}>
            <figure className='imgPizza'><img src={element.imagen} alt={element.nombre} /></figure>
            {/* "&nbsp;" is just a space */}
            <p className='pizza'>
              {/* You do not need <span> but probably you will refactor css */}
              <span>{element.nombre}&nbsp;</span>
              <span className='size'>{element.tamaño}</span>
            </p>
            <p className='cantidad'>{element.cantidad}</p>
          </section>
        );
      } else if (element.promo === true) {
        if (element.grupo === 'Dúos Grandes') {
          return (
            <section className='items' key={index}>
              {/* <section className='imgAndName'> */}
                <figure className='imgPizza'><img src={element.imagen} alt={element.grupo} /></figure>
                <p className='pizza'>{element.grupo}</p>
              {/* </section> */}
              <section className='promo'>
                <p>{element.pizza1.cantidad} {element.pizza1.name} {element.pizza1.size_deLaCasa || element.pizza1.size_tradicional}</p>
                <p>{element.pizza2.cantidad} {element.pizza2.name} {element.pizza2.size_deLaCasa || element.pizza2.size_tradicional}</p>
              </section>
            </section>
          );
        } else {
          return (
            <section className='items' key={index}>
              {/* <section className='imgAndName'> */}
                <figure className='imgPizza'><img src={element.imagen} alt={element.grupo} /></figure>
                <p className='pizza'>{element.grupo}</p>
              {/* </section> */}
              <section className='promo'>
                <p>{element.pizza.cantidad} {element.pizza.name} {element.pizza.size_casa || element.pizza.size_tradicional}</p>
                <p>{element.complemento.cantidad} {element.complemento.name}</p>
                <p>{element.bebida.cantidad} {element.bebida.name} {element.bebida.description}</p>
              </section>
            </section>
          );
        }
      }
    })
  ) : null;

  const entrega = (status === 'PAID' && infoUser.entrega === 'local') ?
  (
    <React.Fragment>
      <div className='svgAddress'>
        {/* To edit size of svg, modify the third number of viewBox with width in css */}
        <a href="https://www.google.com/maps/place/Pizzarela+Artesanal/@-12.0740405,-77.0526108,17z/data=!4m5!3m4!1s0x0:0xb065abdcecffc10!8m2!3d-12.0734874!4d-77.051577" target="_blank" rel='noreferrer'>
          <svg id="placeHolderLocal" viewBox="0 0 700 639.83">
            <path className="cls-1" d="M398.69,122.36C258.77,122.36,173,249,180.13,351.12c3.63,51.8,17.12,111.67,
            83.75,217.54,91.91,146,137.93,193.53,137.93,193.53s35.69-27.07,136.77-194.55c64-105.93,
            80.79-165.54,84.77-202.22C634.58,261.76,560.05,122.36,398.69,122.36Zm3.12,314.89a94.11,
            94.11,0,1,1,94.11-94.11h0A94.11,94.11,0,0,1,401.81,437.25Z" transform="translate(-179.72 -122.36)"/>
          </svg>
        </a>
        <p>Jirón Mariscal Luzuriaga 131, Jesús María 15072, Perú</p>
      </div>
      <p className='horario'>
          *Recuerda que nuestro horario es de <span>Domingo a Jueves</span> de <span>12:00 a 22:00</span> horas, <span>Viernes y Sábado</span> de <span>12:00 a 23:00</span> horas.
      </p>
    </React.Fragment>
  )
  : 
  (
    <React.Fragment>
      <p>¡ Tus pizzarelas llegarán en seguida !</p>
      <p>Dirección: {infoUser.distrito}, {infoUser.urbanizacion}, {infoUser.calle}, {infoUser.numero}, apartamento {infoUser.apartamento}, bloque {infoUser.bloque}. {(infoUser.detalles) ? `${infoUser.detalles}.` : null}</p>
    </React.Fragment>
  );

  let operationType;
  // if (status === 'PAID' && 'DEBIT' === 'DEBIT') {
    if (status === 'PAID' && answer.value.transactions[0].operationType === 'DEBIT') {
    operationType = 'Débito';
    // } else if (status === 'PAID' && 'CREDIT' === 'CREDIT') {
  } else if (status === 'PAID' && answer.value.transactions[0].operationType === 'CREDIT') {
    operationType = 'Crédito';
  }

  const effectiveBrand = (status === 'PAID') ? answer.value.transactions[0].transactionDetails.cardDetails.effectiveBrand : null;
  // const effectiveBrand = (status === 'PAID') ? 'VISA' : null;

  return (
    <section className='respuesta'>
      { 
        status === 'PAID' ? 
        (
          <React.Fragment>
            
            <section className='detalleRespuesta'>
              <h2>¡ Tu compra ha sido aceptada !</h2>
              <div className='compraAceptada'>
                <div>
                  {/* <p>Tu Id de transacción: {932264}</p> */}
                  <p>Tu Id de transacción: <span style={{fontWeight: '700'}}>{answer.value.transactions[0].transactionDetails.cardDetails.legacyTransId}</span></p>
                  <p>¡ Muchas gracias, {infoUser.nombres} {infoUser.apellidos} !</p>
                </div>
                <svg id='check' viewBox="-10 -10 340 340">
                  <circle className="cls-1" cx="156.5" cy="156.5" r="151.5"/>
                  <path className="cls-1" d="M212.21,360.53l60.6,64.34,127-152.17" transform="translate(-149.5 -184.12)"/>
                </svg>
              </div>
            </section>
            <section className='infoCliente'>
              <section className='detalleCliente'>
                <h2>Información del cliente:</h2>
                <p>Tipo: {operationType} {effectiveBrand}</p>
                {/* <p>Tarjeta: {'497010XXXXXX0113'}</p> */}
                <p>Tarjeta: {answer.value.transactions[0].transactionDetails.cardDetails.pan}</p>
                {/* <p>Monto: 40.40 soles</p> */}
                <p>Monto: <span style={{fontWeight: '700'}}>{((answer.value.orderDetails.orderTotalAmount) / 100).toFixed(2)} soles</span></p>
              </section>
              <section className='detalleEntrega'>
                <h2>Entrega de tus pizzarelas:</h2>
                {entrega}
              </section>
            </section>
            <section className='detalleProductos'>
              <h2>Tus pizzarelas:</h2>
              {productos}
            </section>
            <p className='duda'>*Si tienes alguna duda, comunícate con nosotros.</p>
            <div>
              <Link to='/'>
              < button className='botonContinuar'>Ir a Inicio</button>
              </Link>
            </div>
          </React.Fragment>
        )
        : 
        (
          <React.Fragment>
            <section className='detalleRespuesta'>
              <h2>Tu compra ha sido rechazada</h2>
              <div className='compraRechazada'>
                <div>
                  {/* <p>Tu Id de transacción rechazada: 932264</p> */}
                  <p>Tu Id de transacción rechazada: <span style={{fontWeight: '700'}}>{answer.value.transactions[0].transactionDetails.cardDetails.legacyTransId}</span></p>
                  <br></br>
                  <p>
                    {infoUser.nombres} {infoUser.apellidos}, este fue el problema con la tarjeta&nbsp; 
                    {answer.value.transactions[0].transactionDetails.cardDetails.pan}:&nbsp; 
                    {/* {infoUser.nombres} {infoUser.apellidos}, este fué el problema con la tarjeta&nbsp; */}
                    {answer.value.transactions[0].detailedErrorMessage || answer.value.transactions[0].errorMessage}
                    {/* {'497010XXXXXX0113'}:&nbsp;  */}
                  </p>
                </div>
                <svg id="x" viewBox="-10 -10 340 340">
                  <circle className="cls-1" cx="156.5" cy="156.5" r="151.5"/>
                  <line className="cls-1" x1="64.89" y1="79.9" x2="248.11" y2="233.1"/>
                  <line className="cls-1" x1="64.89" y1="233.1" x2="248.11" y2="79.9"/>
                </svg>
              </div>
          </section>
          <div>
            <Link to='/carrito'>
              < button className='botonContinuar'>Ir a Carrito</button>
            </Link>
          </div>
          </React.Fragment>
        )
      }
      
    </section>
  )
}

export default Answer;
