import { useState, useEffect } from "react";
import { useDispatch } from 'react-redux'

import { makeStatusIdle } from "../../app/store/pagoSlice";
import User from './user/User'
import Resume from './resume/Resume'

import './infoUser.css'

export const InfoUser = () => {

  const [envio, setEnvio] = useState('0.00')

  const changeEnvio = newEnvio => setEnvio(newEnvio)

  const dispatch = useDispatch();

  useEffect(() => {
      // si el cliente estando en pago, regresa a pizzas para cambiar sus pizzas, o regresa a carrito
      // tendrá que volver a generar un token, PERO por redux payForm.status = 'succeeded' no le permitirá.
      // makeStatusIdel permite genrear un token nuevo, siempre y cuando la perosna no acceda directamente a /pago
      dispatch(makeStatusIdle())
  }, [dispatch])
  
  // You are passing a price delivery from User to display it inside Resume
  // No need to use Redux
  return (
    <section className='infoUser'>
      <section className='sNavigation'>
          <ul className='breadcrumb'>
            <li className='car'>Carrito</li>
            <li className='contacto'>Contacto</li>
            <li className='pago'>Pago</li>
          </ul>
      </section>
      <section className='formResumen'>
        <section className='form'>
          <User onChangeEnvio={changeEnvio}/>
        </section>
        <section className='resumen'>
          <Resume envio={envio}/>
        </section>
      </section>
    </section>
  );
};