import './divPoliticaAlimentos.css';

import divisor from '../../images/divisor.png';

export const DivPoliticaAlimentos = () => {
  return (
    <section className='divPoliticaAlimentos'>
      <h2>Política en alimentos</h2>
      <figure className='divisor'><img src={divisor} alt='divisor' /></figure>
      <p>Conoce cuál es el proceso de nuestras pizzas</p>
    </section>
  );
};