import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';

import { allPizzas } from '../../apis/pizzas';

const initialState = {
  value: [],
  status: 'idle',
  error: null,
}

export const fetchAllPizzas = createAsyncThunk('allPizzas/fetchAllPizzas', async () => {
  const response = await allPizzas();
  return response;
});

const pizzasSlice = createSlice({
  name: 'allPizzas',
  initialState,
  reducers: {},
  extraReducers: {
    [fetchAllPizzas.pending]: (state, action) => {
      state.status = 'loading';
    },
    [fetchAllPizzas.fulfilled]: (state, action) => {
      state.status = 'succeeded';
      state.value = state.value.concat(action.payload);
    },
    [fetchAllPizzas.rejected]: (state, action) => {
      state.status = 'failed';
      state.error = action.error.message;
    },
  }
});

export default pizzasSlice.reducer;
// Taking just "De la casa" pizzas (index 0) from state.pizzas
// result = [index0, inxex1, index2, ... index20] if there are 21 size pizzas
export const selectDeLaCasaPizzas = state => state.pizzas.value[0];
// Taking just "Tradicionales" pizzas (index 1) from state.pizzas
export const selectTradicionalesPizzas = state => state.pizzas.value[1];
export const selectComplementos = state => state.pizzas.value[2];
export const selectBebidas = state => state.pizzas.value[3];
export const selectPromos = state => state.pizzas.value[4];