import { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';

import { toogleBar } from "../../app/store/barToogle";
import { makeStatusIdle } from "../../app/store/pagoSlice";
import { updateProductos } from '../../app/store/infoUserSlice'
import useDisplayCarrito from '../../app/hooks/forCarrito/useDisplayCarrito'
import { imgObj } from './imgObj';
import './carrito.css';

export const Carrito = () => {

  // All products from DB
  const deLaCasaPizzas = useSelector(state => state.pizzas.value[0]);
  const tradicionalesPizzas = useSelector(state => state.pizzas.value[1]);
  const complementosPizzas = useSelector(state => state.pizzas.value[2]);
  const bebidasPizzas = useSelector(state => state.pizzas.value[3]);
  const promosPizzas = useSelector(state => state.pizzas.value[4])

  // Al counter from Redux
  const deLaCasaCounter = useSelector(state => state.counter.deLaCasa);
  const tradicionalesCounter = useSelector(state => state.counter.tradicionales);
  const complementosCounter = useSelector(state => state.counter.complementos);
  const bebidasCounter = useSelector(state => state.counter.bebidas);
  const promosCounter = useSelector(state => state.counter.promos)

  // Local State
  const [pizzas, setPizzas] = useState([
    <h2 key={0} className='vacio'>Tu carrito aún está vacío, <Link to='/'>elije tu pizza favorita 🍕</Link></h2>
  ]);
  const [total, setTotal] = useState(null);
  const [showHorario, setShowHorario] = useState(null);
  const [next, setNext] = useState(null);
  const [breadCrumb, setBreadCrumb] = useState(null)

  // To establish orders inside Pizzarela hours
  const date = new Date();
  const day = date.getDay();
  const hour = date.getHours();

  const dispatch = useDispatch();

  // Render selected pizzas
  // You are abstracting a lot here
  let render0AndToSum1 = [[], []];
  // let render = [];
  // let toSum = [];
  let products = [];
  // const useDisplayCarrito = (x, nombreCounter, render, imgObj, imgName, nombrePizzas, size, toSum, displayedName, products) =>
  
  // ******************************************************************************De la Casa
  let d = deLaCasaPizzas.arrPizzas.length; // d is the number of pizzas for De la Casa
  render0AndToSum1 = useDisplayCarrito(
    d, 
    deLaCasaCounter, 
    render0AndToSum1[0], 
    imgObj, 
    'deLaCasa', 
    deLaCasaPizzas.arrPizzas, 
    'size_casa', 
    render0AndToSum1[1],
    deLaCasaPizzas.name,
    products // To compare in DB
  );
    
  // ******************************************************************************Tradicionales
  let t = tradicionalesPizzas.arrPizzas.length; // d is the number of pizzas for tradicionales
  render0AndToSum1 = useDisplayCarrito(
    t, 
    tradicionalesCounter, 
    render0AndToSum1[0], 
    imgObj, 
    'tradicionales', 
    tradicionalesPizzas.arrPizzas, 
    'size_tradicional', 
    render0AndToSum1[1],
    tradicionalesPizzas.name,
    products // To compare in DB
  );

  // ******************************************************************************Complementos
  for ( let i=1; i <= complementosPizzas.arrComplementos.length; i++) {
    let pizzaObj = (complementosCounter[i] > 0) ? (complementosPizzas.arrComplementos.find(index => index.id === i)) : null;
    complementosCounter[i] > 0 && render0AndToSum1[0].push(
      // <DisplayCarrito
        // You are creating a unique key with id, name and size of each arr of Pizzas
        // since almost everything is the same in your array
      <section className='items' key={`${complementosPizzas.arrComplementos[i-1].id}${complementosPizzas.arrComplementos[i-1].name}`}>
        <figure className='imgPizza'><img src={imgObj['complementos'][i]} alt={`${pizzaObj.name}`} /></figure>
        {/* "&nbsp;" is just a space */}
        <p className='pizza'>
          {/* You do not need <span> but probably you will refactor css */}
          <span>{pizzaObj.name}</span>
        </p>
        <p className='cantidad'>{complementosCounter[i]}</p>
        <p className='precio'>S/. {pizzaObj.price}</p>
        <p className='precioCantidad'>S/. {(pizzaObj.price * complementosCounter[i]).toFixed(2)}</p>
      </section>
    );
    
    complementosCounter[i] > 0 && render0AndToSum1[1].push(Number((pizzaObj.price * complementosCounter[i]).toFixed(2)));
    complementosCounter[i] > 0 && products.push({
      promo: false,
      grupo: complementosPizzas.name,
      id: pizzaObj.id,
      nombre: pizzaObj.name,
      tamaño: null,
      precio: Number(pizzaObj.price),
      cantidad: complementosCounter[i],
      subTotal: (pizzaObj.price * complementosCounter[i]).toFixed(2),
      imagen: imgObj['complementos'][i],
    })
  }

  // ******************************************************************************Bebidas
  for ( let i=1; i <= bebidasPizzas.arrBebidas.length; i++) {
    let pizzaObj = (bebidasCounter[i] > 0) ? (bebidasPizzas.arrBebidas.find(index => index.id === i)) : null;
    bebidasCounter[i] > 0 && render0AndToSum1[0].push(
      // <DisplayCarrito
        // You are creating a unique key with id, name and size of each arr of Pizzas
        // since almost everything is the same in your array
      <section className='items' key={`${bebidasPizzas.arrBebidas[i-1].id}${bebidasPizzas.arrBebidas[i-1].name}`}>
        <figure className='imgPizza'><img src={imgObj['bebidas'][i]} alt={`${pizzaObj.name}`} /></figure>
        {/* "&nbsp;" is just a space */}
        <p className='pizza'>
          {/* You do not need <span> but probably you will refactor css */}
          <span>{pizzaObj.name}&nbsp;</span>
          <span className='size'>{pizzaObj['description']}</span>
        </p>
        <p className='cantidad'>{bebidasCounter[i]}</p>
        <p className='precio'>S/. {pizzaObj.price}</p>
        <p className='precioCantidad'>S/. {(pizzaObj.price * bebidasCounter[i]).toFixed(2)}</p>
      </section>
    );
    
    bebidasCounter[i] > 0 && render0AndToSum1[1].push(Number((pizzaObj.price * bebidasCounter[i]).toFixed(2)));
    bebidasCounter[i] > 0 && products.push({
      promo: false,
      grupo: bebidasPizzas.name,
      id: pizzaObj.id,
      nombre: pizzaObj.name,
      tamaño: pizzaObj.description,
      precio: Number(pizzaObj.price),
      cantidad: bebidasCounter[i],
      subTotal: (pizzaObj.price * bebidasCounter[i]).toFixed(2),
      imagen: imgObj['bebidas'][i],
    })
  }

  // ******************************************************************************Promos
  let renderArr = [];
  for (let i = 1; i <= promosPizzas.arrPromociones.length; i++) {
    if (promosCounter[i].length > 0) {
      renderArr = promosCounter[i].map( (element, index) => {
        render0AndToSum1[1].push(Number(element.precio))
        if (element.grupo === 'Dúos Grandes') {
          products.push({
            promo: true,
            grupo: element.grupo,
            id: element.id,
            pizza1: element.pizza1,
            pizza2: element.pizza2,
            precio: Number(element.precio),
            cantidad: 1,
            subTotal: element.precio,
            imagen: imgObj.promos[i],
          })
          return (
            <section className='items' key={`${index}${element.grupo}`}>
              <figure className='imgPizza'><img src={imgObj.promos[i]} alt={`${element.grupo}`} /></figure>
              <p className='namePromo'>{element.grupo}</p>
              <section className='promo'>
                <p>{element.pizza1.cantidad} {element.pizza1.name} {element.pizza1.size_deLaCasa || element.pizza1.size_tradicional}</p>
                <p>{element.pizza2.cantidad} {element.pizza2.name} {element.pizza2.size_deLaCasa || element.pizza2.size_tradicional}</p>
              </section>
              <p className='precioCantidad'>S/. {element.precio}</p>
            </section>
          );
        } else {
          products.push({
            promo: true,
            grupo: element.grupo,
            id: element.id,
            pizza: element.pizza,
            bebida: element.bebida,
            complemento: element.complemento,
            precio: Number(element.precio),
            cantidad: 1,
            subTotal: element.precio,
            imagen: imgObj.promos[i],
          })
          return (
            <section className='items' key={`${index}${element.grupo}`}>
              <figure className='imgPizza'><img src={imgObj.promos[i]} alt={`${element.grupo}`} /></figure>
              <p className='namePromo'>{element.grupo}</p>
              <section className='promo'>
                <p>{element.pizza.cantidad} {element.pizza.name} {element.pizza.size_casa || element.pizza.size_tradicional}</p>
                <p>{element.bebida.cantidad} {element.bebida.name} {element.bebida.description}</p>
                <p>{element.complemento.cantidad} {element.complemento.name}</p>
              </section>
              <p className='precioCantidad'>S/. {element.precio}</p>
            </section>
          );
        }
      });
      render0AndToSum1[0] = render0AndToSum1[0].concat(renderArr);
    }
  }

  // When hit 'carrito' close the menu
  useEffect(() => {
    dispatch(toogleBar('close'));
  })

  useEffect(() => {
    // If selected pizzas, show more info
    if (render0AndToSum1[0].length !== 0) {
      setBreadCrumb(
        <section className='sNavigation'>
          <ul className='breadcrumb'>
            <li className='car'>Carrito</li>
            <li className='contacto'>Contacto</li>
            <li className='pago'>Pago</li>
          </ul>
        </section>
      );
      setPizzas(render0AndToSum1[0]);
      setTotal(
        <p className='total'>Total: S/. {(render0AndToSum1[1].reduce((a, b) => a + b)).toFixed(2)}</p>
      );
      setShowHorario(
        <p className='horario'>
          *Recuerda que nuestro horario es de <span>Domingo a Jueves</span> de <span>12:00 a 22:00</span> horas, <span>Viernes y Sábado</span> de <span>12:00 a 23:00</span> horas.
        </p>
      );

      if ((day <= 4 && 21 >= hour && hour >= 12) || 
      (day >= 5 && 22 >= hour && hour >= 12)) {
        setNext(
          <div>
            <Link to='/informacion'>
              <button className='botonContinuar'>Continuar</button>
            </Link>
          </div>
        )
      } else {
        setNext(
          <div>
            <Link to='/'>
              <button className='botonContinuar'>Fuera de Horario</button>
            </Link>
          </div>
        )
      }
      
      dispatch(updateProductos(products));

      // si el cliente estando en pago, regresa a pizzas para cambiar sus pizzas, o regresa a carrito
      // tendrá que volver a generar un token, PERO por redux payForm.status = 'succeeded' no le permitirá.
      // makeStatusIdel permite genrear un token nuevo, siempre y cuando la perosna no acceda directamente a /pago
      dispatch(makeStatusIdle())
    }
    // You just want it to run just once after mounting
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return(
    window.scrollTo(0, 0),
    <section className='carrito'>
      {breadCrumb}
      {pizzas}
      {total}
      {showHorario}
      {next}
    </section>
  );
};