import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';

import { payForm } from '../../apis/payForm';

const initialState = {
  value: {},
  status: 'idle',
  error: null,
}

export const fetchPayForm = createAsyncThunk('payForm/fetchPayForm', async (infoUser) => {
  const response = await payForm(infoUser);
  return response;
});

const pagoSlice = createSlice({
  name: 'payForm',
  initialState,
  reducers: {
    makeStatusIdle: (state, action) => void(state.status = 'idle'),
  },
  extraReducers: {
    [fetchPayForm.pending]: (state, action) => {
      state.status = 'loading';
    },
    [fetchPayForm.fulfilled]: (state, action) => {
      state.status = 'succeeded';
      state.value = action.payload;
    },
    [fetchPayForm.rejected]: (state, action) => {
      state.status = 'failed';
      state.error = action.error.message;
    },
  }
});

export default pagoSlice.reducer;

export const { makeStatusIdle } = pagoSlice.actions;