import './recomendacion.css';

import littlePizza from '../../../images/recomendacion/littlePizza.png';
import divisor from '../../../images/divisor.png';
import pizzaLomazo from '../../../images/recomendacion/pizzaLomazo.png';

export const Recomendacion = () => {
  return(
    <section className='recomendacion'>
      <section className='pizza'>
        <figure className='littlePizza'><img src={littlePizza} alt='littlePizza' /></figure>
        <h2>Recomendación del Chef</h2>
        <figure className='divisor'><img src={divisor} alt='divisor' /></figure>
        <h3>¡ Lomazo !</h3>
        <p>Nada tan deliciosa como nuestra pizza Lomazo</p>
        <p>Salsa de tomate, queso mozzarella, lomo saltado tradicional.</p>
      </section>
      <section className='imagen'>
        <figure><img src={pizzaLomazo} alt='pizzaLomazo' /></figure>
      </section>
    </section>
  );
};