import React, { useEffect} from 'react'
import { useSelector, useDispatch } from 'react-redux'

import { updateTotalPrice } from "../../../app/store/infoUserSlice";

import './resume.css'

const Resume = ({ envio }) => {

  const productos = useSelector(state => state.infoUser.productos);

  const dispatch = useDispatch();

  let render = [];
  let toSum = [];

  render = productos.map((element, index) => {
    return (
      <section className='items' key={index}>
        <figure className='imgPizza'><img src={element.imagen} alt={element.promo ? element.grupo : element.nombre} /></figure>
        {/* "&nbsp;" is just a space */}
        <p className='pizza'>
          {/* You are adding span to display: block in <Resume /> component */}
          {
            element.promo ? 
            (<span>{element.grupo}</span>) 
            : 
            (
              <React.Fragment>
                <span>{element.nombre}&nbsp;</span>
                <span className='size'>{element.tamaño}</span>
              </React.Fragment>
            )
          }
          
        </p>
        <p className='cantidad'>{element.cantidad}</p>
        <p className='precio'>S/. {element.precio}</p>
        <p className='precioCantidad'>S/. {element.subTotal}</p>
      </section>
    );
  });
      
  toSum = productos.map(element => Number(element.subTotal));
  const subTotal = (toSum.reduce((a, b) => a + b)).toFixed(2);
    
  // If selected pizzas, show more info
    
  useEffect(() => {
    window.scrollTo(0, 0)
  }, []);

  // It's necessary to use another effect because "envio" will change depending on distrito selection
  useEffect(() => {
    // useEffect will render after mounting, therefore subTotal will be null the fisrt render
    // do not want to dispath a (null + evio) value
    if (subTotal !== null) {
      // You convert it into string due to validation in server
      const total = ((Number(subTotal) + Number(envio)) * 100).toFixed(0);
      dispatch(updateTotalPrice(total))
    }
  }, [dispatch, envio, subTotal]);

  return (
    
    <section className='resume'>
      <h2>Tus Pizzarelas</h2>
      {render}
      <hr className='hrElementUp' />
      {/* toFixed(n) give a string, therefore subTotal is string */}
      <section className='aSumar'>
        <p className='subTotal'>Subtotal:</p>
        <p className='number'>S/. {subTotal}</p>
      </section>
      <section className='aSumar'>
        <p className='envioPrecio'>Envío:</p>
        <p className='number'>S/. {envio}</p>
      </section>
      <hr />
      {/* Remember Total is a string */}
      <section className='aSumarTotal'>
        <h2 className='totalLetra'>Total:</h2>
        <h2 className='totalNumber'>S/. {(Number(subTotal) + Number(envio)).toFixed(2)}</h2>
      </section>
    </section>
  )
}

export default Resume
