import './MyV.css';

import video from '../../images/videoPizzarela.mp4';
import divisor from '../../images/divisor.png';

export const MyV = () => {
  return(
    <section className='MyV'>
      <section className='info'>
        <h2>Misión</h2>
        <figure className='divisor'><img src={divisor} alt='divisor' /></figure>
        <p>
          Pizzarela Artesanal desea ofrecer a nuestros comensales la variedad perfecta de pizzas,
          en la comodidad de su casa o en nuestro local. Siempre elaborando artesanalmente nuestras
          pizzas con ingredientes frescos y de calidad.
        </p>
        <br></br>
        <br></br>
        <h2>Visión</h2>
        <figure className='divisor'><img src={divisor} alt='divisor' /></figure>
        <p>
          Ser una empresa reconocida en el mercado como una de las mejores pizzerías artesanales
          dentro de la ciudad de Lima, satisfaciendo las necesidades de nuestros clientes y
          manteniendo la calidad de nuestros productos.
        </p>
      </section>
      <section className='video'>
        <video autoPlay loop muted controls>
          <source src={video} type='video/mp4' />
        </video>
      </section>
    </section>
    
  );
};