import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch} from 'react-redux'
import { Link, useHistory } from "react-router-dom";
import KRGlue from '@lyracom/embedded-form-glue';
import sha256 from 'js-sha256'

import dots from '../../app/hooks/forLoading/dots'
import { updateAnswer } from "../../app/store/clientAnswerSlice";
import { resetCounterDeLaCasa,
         resetCounterTradicionales,
         resetCounterComplementos,
         resetCounterBebidas,
         resetCounterPromos } from "../../app/store/counterSlice";
import { fetchPayForm } from "../../app/store/pagoSlice";

import './pago.css';

const endpoint = 'https://static.micuentaweb.pe/';
const publicKey = '21907733:testpublickey_AiIoQKeFjRN3KXCISJs5gg0CUgCzOoiFXro3ppQZkAbTl';

function Pago() {

  const [errorForm, setErrorForm] = useState(null)

  const infoUser = useSelector(state => state.infoUser)
  const payFormStatus = useSelector(state => state.payForm.status);
  const payFormValue = useSelector(state => state.payForm.value)
  const error = useSelector(state => state.payForm.error);

  const dispatch = useDispatch();
  const history = useHistory();

  useEffect(() => {
    if (payFormStatus === 'idle' && infoUser.productos !== null) {
      dispatch(fetchPayForm(infoUser));
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  },[dispatch, payFormStatus])

  useEffect(() => {
    if (payFormStatus === 'succeeded') {
      const formToken = payFormValue.answer.formToken;
      // console.log(formToken);
      KRGlue.loadLibrary(endpoint, publicKey) /* Load the remote library */
      .then(({ KR }) => {
        return KR.setFormConfig({
          /* set the minimal configuration */
          formToken: formToken,
          "kr-language": "es-ES", /* to update initialization parameter */
          // When using post, the pege in the front-end do not load. I think post is just for back-end. 
          // However, you have the IPN for the back-end, thus, you can simulate the IPN with this post (trick) 
          // At the moment of writing this, you do not have acces to the IPN becuase back-office does not allow localhost
          // "kr-post-url-success": "http://localhost:3005/payForm/status",	 	/* URL a la que se envía el formulario (método POST) en caso de éxito */
          // "kr-get-url-success": "http://localhost:3000/aceptada",	 	/* URL a la que se envía el formulario (método POST) en caso de éxito */
          // "kr-post-url-refused": "http://localhost:3005/payForm/status",	 	/* URL que llamar cuando todos los intentos han fallado (método POST) */
          // "kr-get-url-refused": "http://localhost:3000/rechazada",	 	/* URL que llamar cuando todos los intentos han fallado (método POST) */
        })
      })
      .then(({ KR }) => {
        return KR.addForm("#myPaymentForm")
      }) /* add a payment form  to myPaymentForm div*/
      .then(({ KR, result }) => {
        // console.log(KR)
        // console.log(result)
        return KR.showForm(result.formId)
      })
      .then(({ KR }) => {
        return KR.onSubmit(e => {
          const hash = sha256.hmac("EqDeVRdH5kWWXdh2JYNWRTYhFIurpDqovg1i44Wu2wWpq", e.rawClientAnswer);
          if (hash !== e.hash) {
            setErrorForm(`La respuesta original a su pago fué modificado por terceros. Por favor, comuníquese con Pizzarela Artesanal.`)
            throw new Error('Firma inválida');
          }
          if (e.clientAnswer.orderStatus === "PAID") {
            dispatch(updateAnswer(e.clientAnswer));
            history.push('/información de pago');
            dispatch(resetCounterDeLaCasa());
            dispatch(resetCounterTradicionales());
            dispatch(resetCounterComplementos());
            dispatch(resetCounterBebidas());
            dispatch(resetCounterPromos());
          } else {
            dispatch(updateAnswer(e.clientAnswer));
            history.push('/información de pago');
          }
        })
      })
      .catch(err => {
        return setErrorForm(`${err} (Ups, algo pasó. Inténtalo de nuevo.)`)
      }); 
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [payFormStatus])

  let renderPago
  if ( payFormStatus === 'idle') {
    renderPago = <h2 key={0} className='vacio'>Tu carrito aún está vacío, <Link to='/'>elije tu pizza favorita 🍕</Link></h2>;
  } else if ( payFormStatus === 'loading') {
    renderPago = (
      <React.Fragment>
        <section className='sNavigation'>
          <ul className='breadcrumb'>
            <li className='car'>Carrito</li>
            <li className='contacto'>Contacto</li>
            <li className='pago'>Pago</li>
          </ul>
        </section>
        <section className='loading'>
          <h2>Cargando Pago</h2>
          {dots()}
        </section>
      </React.Fragment>
    );
  } else if ( payFormStatus === 'succeeded') {
    renderPago = (
      <React.Fragment>
        <section className='sNavigation'>
          <ul className='breadcrumb'>
            <li className='car'>Carrito</li>
            <li className='contacto'>Contacto</li>
            <li className='pagoBreadCrumb'>Pago</li>
          </ul>
        </section>
        <div className="form">
          <h2>Formulario de Pago</h2>
          <div className="container">
            <div id="myPaymentForm"></div>
          </div>
          <div>{errorForm}</div>
        </div>
        <p className='recomendacion'>
          * Recomendamos encarecidamente que toda compra online la realice en sus propios dispositivos de confianza
          y no en dispositivos públicos.
        </p>
      </React.Fragment>
    );
  } else if ( payFormStatus === 'failed') {
    renderPago = <section>{error}</section>
  }

  return (
      window.scrollTo(0, 0),
      <section className='pago'>
        {renderPago}
      </section>
    );
}

export default Pago
