import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';

import { toogleBar } from "../../app/store/barToogle";
import logo from '../../images/Pizzarela-Artesanal.png';
import './header.css';

export const Header = () => {
  
  const barToogle = useSelector(state => state.barToogle)
  const allPizzas = useSelector(state => state.pizzas.value);
  const pizzasStatus = useSelector(state => state.pizzas.status);
  const error = useSelector(state => state.pizzas.error);
  const allCounter = useSelector(state => state.counter);

  const [filter, setFilter] = useState("opacity(10%) brightness(10%)");
  const [uniCode2, setUniCode2] = useState(709);

  const dispatch = useDispatch();

  // This part is for rendering .botonCarrito if allCounter[i] > 0;
  // d is the last id of deLaCasa pizzas or the number of pizzas
  // WATCH OUT! You are comparing undefine due to const d,
  // complementos, bebidas, promos do not have to many properties
  useEffect(() => {
    const d = 21;
    const arrNumPizzas = [];
    for ( let i=1; i <= d; i++) {
      allCounter.deLaCasa[i] > 0 && arrNumPizzas.push(allCounter[i]);
      allCounter.tradicionales[i] > 0 && arrNumPizzas.push(allCounter[i]);
      allCounter.complementos[i] > 0 && arrNumPizzas.push(allCounter[i]);
      allCounter.bebidas[i] > 0 && arrNumPizzas.push(allCounter[i]);
    }
    const arrNumPromos = [];
    for (let i = 1; i <= 5; i++) {
      allCounter.promos[i].length > 0 && arrNumPromos.push(true);
    }
    if (arrNumPizzas.length !== 0 || arrNumPromos.length !== 0) {
      setFilter("opacity(100%) brightness(100%)");
    } else {
      setFilter("opacity(10%) brightness(10%)");
    }
  }, [allCounter])

  let content = [];

  if (pizzasStatus === 'loading') {
    content = <p>Cargando Pizzas...</p>
  } else if (pizzasStatus === 'succeeded') {
    // You do not want the las element of the array
    for (let i = 0; i < allPizzas.length -2; i++) {
      content.push(<li key={i}><Link to={`/${allPizzas[i].name}`}>{allPizzas[i].name}</Link></li>)
    }
  } else if (pizzasStatus === 'error') {
    content = <div>{error}</div>
  }

  const handleToogle = () => {
    dispatch(toogleBar());
  };

  const changeUniCode2 = () => {
    if (uniCode2 === 709){
      setUniCode2(708)
    } else if ( uniCode2 === 708) {
      setUniCode2(709)
    }
  };
  

  return (
    <header>
      <Link to='/'><img src={logo} alt="Pizzarela-Artesanal" /></Link>
      <label htmlFor="boton" className="iconBarra" >{String.fromCharCode(barToogle.unicode)}</label>
      <input type="checkbox" id="boton" className="inputHeader" checked={barToogle.value} onChange={handleToogle}></input>
      <nav className="navega">
        <ul>
          <li>
            <Link to='/' tabIndex='0'>Inicio</Link>
          </li>
          <li>
            <Link to='/nosotros' tabIndex='0'>Nosotros</Link>
          </li>
          <li>
            <label htmlFor="boton-especialidad" className="muestra" onClick={changeUniCode2} tabIndex='0'>Pizzas {String.fromCharCode(uniCode2)}</label>
            <Link to='#' className='despues' tabIndex='0'>Pizzas</Link>
            <input type="checkbox" id="boton-especialidad" className="inputHeader" />
            <ul className="subMenu">
             {content}
            </ul>
          </li>
          <li>
            <Link to='/promociones' tabIndex='0'>Promociones</Link>
          </li>
          <li>
            <Link to='/contactanos' tabIndex='0'>Contáctanos</Link>
          </li>
          <li className='carrito'>
           <Link to='/carrito'>
            <svg id="Layer_2" 
              filter={filter} data-name="Layer 2" viewBox="0 0 332.1 318.85"
              fill="none"   strokeLinecap="round"  strokeMiterlimit="10"  strokeWidth="30px">
              <path className="cls-1" d="M152.47,207.74l45.87,18.61a16.31,16.31,0,0,1,9.89,12.11c9.32,49.85,19.57,101.79,28.94,151.76a16.31,16.31,0,0,0,15.88,13.28c52.69.52,104.53-.9,157.24-.34a16.28,16.28,0,0,0,15.58-11.11l35.84-106.72a16.3,16.3,0,0,0-14.64-21.46l-184.3-9.15" transform="translate(-141.47 -196.74)"/>
              <path className="cls-1" d="M424.81,448.77l-183.49-.68" transform="translate(-141.47 -196.74)"/>
              <line className="cls-1" x1="99.85" y1="251.34" x2="115.51" y2="206.79"/>
              <circle className="cls-1" cx="130.15" cy="283.68" r="24.17"/>
              <circle className="cls-1" cx="247.09" cy="283.68" r="24.17"/>
            </svg>
           </Link>
          </li>
        </ul>
    </nav>
    </header>
    
  );
};