import { createSlice } from '@reduxjs/toolkit';

// d is the number of size pizzas for deLaCasa
let d = 21;
let deLaCasa = {};
for (let i=1; i <= d; i++) {
  deLaCasa[i] = 0;
}

// t is the number of size pizzas for tradicionales
let t = 21;
let tradicionales = {};
for (let i=1; i <= t; i++) {
  tradicionales[i] = 0;
}

// c is the number of size pizzas for complementos
let c = 4;
let complementos = {};
for (let i=1; i <= c; i++) {
  complementos[i] = 0;
}

// b is the number of size pizzas for bebidas
let b = 10;
let bebidas = {};
for (let i=1; i <= b; i++) {
  bebidas[i] = 0;
}

// p is the number of promos 
let p = 5
let promos = {};
for (let i = 1; i <= p; i ++) {
  promos[i] = [];
}

const counter = createSlice({
  name: 'counter',
  initialState: {
    deLaCasa,
    tradicionales,
    complementos,
    bebidas,
    promos,
  },
  reducers: {
    // action: {
    //   type: counter/... ,
    //   payload:{name: NOMBRE_DE_PIZZA, id: NUMERO_DE_PIZZA}
    // }
    increment: (state, action) => {
      if (state[action.payload.name][action.payload.id] >=20) {
        state[action.payload.name][action.payload.id] = 20;
      } else {
        state[action.payload.name][action.payload.id] += 1;
      }
    },
    decrement: (state, action) => {
      if (state[action.payload.name][action.payload.id] <= 0){
        state[action.payload.name][action.payload.id] = 0;
      } else {
        state[action.payload.name][action.payload.id] -= 1;
      }
    },
    pushPromo: (state, action) => {
      if (state.promos[action.payload.id].length < 20) {
        state.promos[action.payload.id].push(action.payload.info);
      }
    },
    popPromo: (state, action) => {
      if (state.promos[action.payload.id].length !== 0) {
        state.promos[action.payload.id].pop();
      }
    },
    resetCounterDeLaCasa: state => void(state.deLaCasa = deLaCasa),
    resetCounterTradicionales: state => void(state.tradicionales = tradicionales),
    resetCounterComplementos: state => void(state.complementos = complementos),
    resetCounterBebidas: state => void(state.bebidas = bebidas),
    resetCounterPromos: state => void(state.promos = promos),
  }
});

export default counter.reducer;

export const {
  increment, 
  decrement,
  pushPromo,
  popPromo,
  resetCounterDeLaCasa,
  resetCounterTradicionales,
  resetCounterComplementos,
  resetCounterBebidas,
  resetCounterPromos,
} = counter.actions;
