import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { 
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom';

import { Header } from './components/header&footer/header';
import { Inicio } from './components/inicio/Inicio';
import { Nosotros } from './components/nosotros/Nosotros';
import { DeLaCasa } from './components/pizzas/deLaCasa/DeLaCasa';
import { Tradicionales } from './components/pizzas/tradicionales/Tradicionales';
import Complementos from './components/pizzas/complementos/Complementos'
import Bebidas from './components/pizzas/bebidas/Bebidas'
import Promociones from './components/promociones/Promociones'
import { Footer } from './/components/header&footer/Footer';
import { Contactanos } from './components/contactanos/Contactanos';
import { Carrito } from './components/carrito/Carrito';
import {InfoUser} from './components/infoUser/InfoUser'
import Pago from './components/pago/Pago'
import Answer from './components/pago/Answer'

import { fetchAllPizzas } from './app/store/pizzasSlice';

import './app.css';

function App() {

  const pizzasStatus = useSelector(state => state.pizzas.status);
  
  const dispatch = useDispatch();
  
  useEffect(() => {
    if (pizzasStatus === 'idle') {
      dispatch(fetchAllPizzas());
    }
  }, [pizzasStatus, dispatch]);

  return (
    <Router>
      <Header />
      <div className="App">
        <Switch>
          <Route exact path='/' component={Inicio} />
          <Route exact path='/nosotros' component={Nosotros} />
          <Route exact path='/De la Casa' component={DeLaCasa} />
          <Route exact path='/Tradicionales' component={Tradicionales} />
          <Route exact path='/Complementos' component={Complementos} />
          <Route exact path='/Bebidas' component={Bebidas} />
          <Route exact path='/contactanos' component={Contactanos} />
          <Route exact path='/promociones' component={Promociones} />
          <Route exact path='/carrito' component={Carrito} />
          <Route exact path='/informacion' component={InfoUser} />
          <Route exact path='/pago' component={Pago} />
          <Route exact path='/información de pago' component={Answer} />
        </Switch>
      </div>
      <Footer />
    </Router>
  );
}

export default App;