import './politicaAlimentos.css';

import divisor from '../../images/divisor.png';
import pizzaLomazo from '../../images/recomendacion/pizzaVerduras.jpg';

export const PoliticaAlimentos = () => {
  return(
    <section className='PoliticaAlimentos'>
      <section className='info'>
        <h2>Pizzarela Artesanal</h2>
        <h3>Conociendo nuestro proceso</h3>
        <figure className='divisor'><img src={divisor} alt='divisor' /></figure>
        <p>La elaboración de nuestras Pizzas es un proceso muy importante, en el cual nuestro chef elije los mejores
          ingredientes tales como prosciutto, tocino ahumado, pepperoni, harina Selecta, queso 100% a base de leche.
          La harina es dejada en reposo el tiempo exacto para poder ser calentada en Horno de Piedra y nuestra salsa
          es preparada a temperatura baja el tiempo necesario para poder ser vertida.
        </p>
      </section>
      <section className='imagen'>
        <figure><img src={pizzaLomazo} alt='pizzaLomazo' /></figure>
      </section>
    </section>
  );
};