import { createSlice } from '@reduxjs/toolkit'

const navBar = createSlice({
  name: 'navBar',
  initialState: { value: false, unicode: 8801 },
  reducers: {
    toogleBar: (state, action) => {
      if (action.payload === 'close') {
        state.value = false
        state.unicode = 8801
      } else {
        if (state.value) {
          state.value = false
          state.unicode = 8801
        } else {
          state.value = true
          state.unicode = 215
        }
      }
    }
  },
});

export default navBar.reducer;

export const { toogleBar } = navBar.actions;