import { useState } from 'react'
import { useSelector, useDispatch } from "react-redux"
import { useHistory } from "react-router-dom";
import { Formik, Field, ErrorMessage} from 'formik'
import * as Yup from 'yup'

import {
  updateNombres,
  updateApellidos,
  updateEmail,
  updateEntrega,
  updateDistrito,
  updateUrbanizacion,
  updateTelefono,
  updateCalle,
  updateNumero,
  updateApartamento,
  updateBloque,
  updateDetalles,
} from '../../../app/store/infoUserSlice'

import './user.css'

let initialValues = {
  nombres: '',
  apellidos: '',
  email: '',
  entrega: 'delivery',
  pais: 'Peru',
  departamento: 'Lima',
  provincia: 'Lima',
  distrito: '',
  urbanizacion: '',
  telefono: '',
  calle: '',
  numero: '',
  apartamento: '',
  bloque: '',
  detalles: '',
}; 

const validationSchema1 = Yup.object({
  nombres: Yup.string()
  .matches(/^[a-zA-ZÀ-ÿ ]+$/, 'Solo letras')
  .max(40, '40 caracteres o menos')
  .required('Requerido'),
  apellidos: Yup.string()
  .matches(/^[a-zA-ZÀ-ÿ ]+$/, 'Solo letras')
  .max(40, '40 caracteres o menos')
  .required('Requerido'),
  email: Yup.string()
  .email('Correo inválido')
  .max(50, '50 caracteres o menos')
  .required('Requerido'),
  entrega: Yup.string().required('Requerido'),
  pais: Yup.string().required('Requerido'),
  departamento: Yup.string().required('Requerido'),
  provincia: Yup.string().required('Requerido'),
  distrito: Yup.string().required('Requerido'),
  urbanizacion: Yup.string()
  .matches(/^[a-zA-ZÀ-ÿ0-9., ]+$/, 'Solo letras y/o números')
  .max(30, '30 caracteres o menos')
  .required('Requerido'),
  telefono: Yup.string()
  .matches('(^\\d{7}$|^\\d{9}$)', '7 o 9 números sin espacios')
  .required('Requerido'),
  calle: Yup.string()
  .matches(/^[a-zA-ZÀ-ÿ0-9., ]+$/, 'Solo letras y/o números')
  .max(100, '100 caracteres o menos')
  .required('Requerido'),
  numero: Yup.string()
  .matches(/^[a-zA-ZÀ-ÿ0-9., ]+$/, 'Solo letras y/o números')
  .max(20, '20 caracteres o menos')
  .required('Requerido'),
  apartamento: Yup.string()
  .matches(/^[a-zA-ZÀ-ÿ0-9., ]+$/, 'Solo letras y/o números')
  .max(20, '20 caracteres o menos'),
  bloque: Yup.string()
  .matches(/^[a-zA-ZÀ-ÿ0-9., ]+$/, 'Solo letras y/o números')
  .max(20, '20 caracteres o menos'),
  detalles: Yup.string()
  .max(100, '100 caracteres o menos')
  .matches(/^[a-zA-ZÀ-ÿ0-9., ]+$/, 'Solo letras y/o números'),
});

const validationSchema2 = Yup.object({
  nombres: Yup.string()
  .matches(/^[a-zA-ZÀ-ÿ ]+$/, 'Solo letras')
  .max(40, '40 caracteres o menos')
  .required('Requerido'),
  apellidos: Yup.string()
  .matches(/^[a-zA-ZÀ-ÿ ]+$/, 'Solo letras')
  .max(40, '40 caracteres o menos')
  .required('Requerido'),
  email: Yup.string()
  .email('Correo inválido')
  .required('Requerido'),
  entrega: Yup.string().required('Requerido'),
});

const User = ({ onChangeEnvio }) => {

  const [validationSchema, setValidationSchema] = useState(validationSchema1);
  const [distrito, setDistrito] = useState('');

  // delivery is an arr
  const delivery = useSelector(state => state.pizzas.value[5].arrDelivery);

  const dispatch = useDispatch();
  const history = useHistory();

  const deliveryPrice = distritoValue => {
    // eslint-disable-next-line default-case
    switch (distritoValue) {
      case delivery[0].name:
        onChangeEnvio(delivery[0].price);
        break;
      case delivery[1].name:
        onChangeEnvio(delivery[1].price);
        break;
      case delivery[2].name:
        onChangeEnvio(delivery[2].price);
        break;
      case delivery[3].name:
        onChangeEnvio(delivery[3].price);
        break;
      case delivery[4].name:
        onChangeEnvio(delivery[4].price);
        break;
      case delivery[5].name:
        onChangeEnvio(delivery[5].price);
        break;
      case delivery[6].name:
        onChangeEnvio(delivery[6].price);
        break;
      case delivery[7].name:
        onChangeEnvio(delivery[7].price);
        break;
    };
  };

  const handleDelivery = e => {
    // You created a custom attribute "data-distrito" in <select> to update when going back to delivery from local.
    // console.log('e.target.valuedistrito:', e.target.getAttribute('data-distrito'));
    const distritoValue = e.target.getAttribute('data-distrito')
    setValidationSchema(validationSchema1);
    deliveryPrice(distritoValue);
  };
  
  const handleLocal = () => {
    onChangeEnvio("0.00");
    setValidationSchema(validationSchema2);
  };

  const handleChangeDistrito = e => {
    const distritoValue = e.target.value;
    // console.log('e object', e)
    // console.log('formik object', formik)
    // using setDistrito because of custom onChange inside Formik <select>
    setDistrito(distritoValue);
    deliveryPrice(distritoValue);
  };

  const onSubmit = values => {
    // You will need to dispatch depending of values.entrega
    // You are not dispatching a obj with all values
    // because you want to respect null values inside initialState in redux
    // Empy strings are not the same to null in postgreSQL
    // console.log('Form data', values);
    // console.log(values.nombres)
    if (values.entrega === 'local') {
      dispatch(updateNombres(values.nombres));
      dispatch(updateApellidos(values.apellidos));
      dispatch(updateEmail(values.email));
      dispatch(updateEntrega(values.entrega));
      dispatch(updateDistrito(null));
      dispatch(updateUrbanizacion(null));
      dispatch(updateTelefono(null));
      dispatch(updateCalle(null));
      dispatch(updateNumero(null));
      dispatch(updateApartamento(null));
      dispatch(updateBloque(null));
      dispatch(updateDetalles(null));
    } else if (values.entrega === 'delivery') {
      dispatch(updateNombres(values.nombres));
      dispatch(updateApellidos(values.apellidos));
      dispatch(updateEmail(values.email));
      dispatch(updateEntrega(values.entrega));
      dispatch(updateDistrito(values.distrito));
      dispatch(updateUrbanizacion(values.urbanizacion));
      dispatch(updateTelefono(values.telefono));
      dispatch(updateCalle(values.calle));
      dispatch(updateNumero(values.numero));
      if (values.apartamento) dispatch(updateApartamento(values.apartamento));
      if (values.bloque) dispatch(updateBloque(values.bloque));
      if (values.detalles) dispatch(updateDetalles(values.detalles));
    }
    // Ir a pago
    history.push('/pago');
  };

  return (
    <section className='formikUser'>
      <Formik
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={onSubmit}
      >
        { (formik) => (
          <form onSubmit={formik.handleSubmit}>
            <section className='who'>
              <h2>¿A quién entregamos tu Pizzarela?</h2>
              <section className='bloque1'>
                <section className='inputNombres'>
                  <input
                    className='campoNombres'
                    name="nombres"
                    type="text"
                    placeholder='Nombres'
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.nombres}
                  />
                  <ErrorMessage name='nombres' component='div'></ErrorMessage>
                </section>
                <section className='inputApellidos'>
                  <Field className='campoApellidos' type='text' name='apellidos' placeholder='Apellidos'></Field>
                  <ErrorMessage name='apellidos' component='div'></ErrorMessage>
                </section>
              </section>
              <section className='inputEmail'>
                <Field className='campoEmail' type='text' name='email' placeholder='Correo eletrónico'></Field>
                <ErrorMessage name='email' component='div'></ErrorMessage>
              </section>
            </section>

            <section className='deliLocal'>
              <h2>¿Delivery o en nuestro local?</h2>
              <section className='bloqueEntrega'>
                <label><Field type="radio" name="entrega" value="delivery" onClick={handleDelivery} data-distrito={formik.values.distrito}/>Delivery</label>
                <label><Field type="radio" name="entrega" value="local" onClick={handleLocal}/>Local</label>
                {/* <div>{values.entrega}</div> */}
              </section>
            </section>

            {formik.values.entrega === 'delivery' ? 
              (
                <section className='address'>
                  <h2>¿A dónde la enviamos?</h2>
                  <section className='infoSelect'>
                    <section className='infoSelectBloque1'>
                      <section className='inputPais'>
                        <label htmlFor='campoPais'>País</label>
                        <Field name="pais" as="select" id='campoPais'>
                          <option value="Peru">Perú</option>
                        </Field>
                      </section>
                      <section className='inputDepartamento'>
                        <label htmlFor='campoDepartamento'>Departamento</label>
                        <Field name="departamento" as="select" id='campoDepartamento'>
                          <option value="Lima">Lima</option>
                        </Field>
                      </section>
                    </section>
                    <section className='infoSelectBloque2'>
                      <section className='inputProvincia'>
                        <label htmlFor='campoProvincia'>Provincia</label>
                        <Field name="provincia" as="select" id='campoProvincia'>
                          <option value="Lima">Lima</option>
                        </Field>
                      </section>
                      <section className='inputDistrito'>
                        {/* {console.log(`Este es el valor de distrito dentro de Formik: ${formik.values.distrito}`)} */}
                        <label htmlFor='campoDistrito'>Distrito</label>
                        <select name='distrito' id='campoDistrito' onChange={handleChangeDistrito} onBlur={formik.handleBlur} value={distrito}>
                          {/* You couldn't update initialValues from the begining, that's why you are updating it here */}
                          {formik.values.distrito = distrito}
                          {/* {console.log(`Este es el valor de distrito dentro de Formik: ${formik.values.distrito}`)}  */}
                          <option value="">Elija opción</option>
                          <option value="Breña">Breña</option>
                          <option value="Jesús María">Jesús María</option>
                          <option value="Lima Cercado">Lima Cercado</option>
                          <option value="Lince">Lince</option>
                          <option value="Magdalena">Magdalena</option>
                          <option value="Pueblo Libre">Pueblo Libre</option>
                          <option value="San Isidro">San Isidro</option>
                          <option value="San Miguel">San Miguel</option>
                        </select>
                        {formik.touched.distrito && formik.errors.distrito ? <div>{formik.errors.distrito}</div> : null}
                      </section>
                    </section>
                  </section>
                  <section className='bloque1'>
                    <section className='inputUrbanizacion'>
                      <Field className='campoUrbanizacion' type='text' name='urbanizacion' placeholder='Urbanización'></Field>
                      <ErrorMessage name='urbanizacion' component='div'></ErrorMessage>
                    </section>
                    <section className='inputTelefono'>
                      <Field className='campoTelefono' type='text' name='telefono' placeholder='Teléfono'></Field>
                      <ErrorMessage name='telefono' component='div'></ErrorMessage>
                    </section>
                  </section>
                  <section className='inputCalle'>
                    <Field className='campoCalle' type='text' name='calle' placeholder='Calle'></Field>
                    <ErrorMessage name='calle' component='div'></ErrorMessage>
                  </section>
                  <section id='numeApartBlo' className='bloque1'>
                    <section className='inputNumero'>
                      <Field className='campoNumero' type='text' name='numero' placeholder='Número'></Field>
                      <ErrorMessage name='numero' component='div'></ErrorMessage>
                    </section>
                    <section className='inputApartamento'>
                      <Field className='campoApartamento' type='text' name='apartamento' placeholder='Apartamento'></Field>
                      <ErrorMessage name='apartamento' component='div'></ErrorMessage>
                    </section>
                    <section className='inputBloque'>
                      <Field className='campoBloque' type='text' name='bloque' placeholder='Bloque'></Field>
                      <ErrorMessage name='bloque' component='div'></ErrorMessage>
                    </section>
                  </section>
                  <section className='inputDetalles'>
                    <Field className='campoDetalles' as='textarea' name='detalles' placeholder='Detalles'></Field>
                    <ErrorMessage name='detalles' component='div'></ErrorMessage>
                  </section>
                </section>
              ) 
              : 
              (
                <section className='svgAddress'>
                  {/* To edit size of svg, modify the third number of viewBox with width in css */}
                  <a href="https://www.google.com/maps/place/Pizzarela+Artesanal/@-12.0740405,-77.0526108,17z/data=!4m5!3m4!1s0x0:0xb065abdcecffc10!8m2!3d-12.0734874!4d-77.051577" target="_blank" rel='noreferrer'>
                    <svg id="placeHolderLocal" viewBox="0 0 700 639.83">
                      <path className="cls-1" d="M398.69,122.36C258.77,122.36,173,249,180.13,351.12c3.63,51.8,17.12,111.67,
                      83.75,217.54,91.91,146,137.93,193.53,137.93,193.53s35.69-27.07,136.77-194.55c64-105.93,
                      80.79-165.54,84.77-202.22C634.58,261.76,560.05,122.36,398.69,122.36Zm3.12,314.89a94.11,
                      94.11,0,1,1,94.11-94.11h0A94.11,94.11,0,0,1,401.81,437.25Z" transform="translate(-179.72 -122.36)"/>
                    </svg>
                  </a>
                  <p>Jirón Mariscal Luzuriaga 131, Jesús María 15072, Perú</p>
                </section>
              )
            }

            {/* {console.log('Errors', formik.errors)} */}

            <button type="submit">Ir a pago</button>
          </form>
        )}
      </Formik>
    </section>
  )
}

export default User
