import React, {useState, useEffect} from 'react'
import { useSelector } from 'react-redux' 
import { Link } from 'react-router-dom';

import { selectPromos, 
         selectDeLaCasaPizzas, 
         selectTradicionalesPizzas, 
         selectComplementos, 
         selectBebidas } from "../../app/store/pizzasSlice"; 
import { Portada  } from "../pizzas/secciones/Portada";
import dots from '../../app/hooks/forLoading/dots'
import DisplayPromo from '../../app/hooks/forPromos/DisplayPromo'
import { imgArr } from "./imgPromocionesArr";

import './promociones.css'

function Promociones() {

  const pizzasStatus = useSelector(state => state.pizzas.status)
  const error = useSelector(state => state.pizzas.error)
  const promos = useSelector(selectPromos)
  const casa  = useSelector(selectDeLaCasaPizzas)
  const tradicionales = useSelector(selectTradicionalesPizzas)
  const complementos = useSelector(selectComplementos)
  const bebidas = useSelector(selectBebidas)
  const promosCounter = useSelector(state => state.counter.promos)

  const [showCarrito, setShowCarrito] = useState(null);

  let renderPromo = [];
  let renderPortada;
  let lengthOfArrPromociones;

  if (pizzasStatus === 'loading') {

    renderPromo = (
      <section className='loading'>
        <h2>Cargando Pizzas</h2>
        {dots()}
      </section>
    )

  } else if (pizzasStatus === 'succeeded') {

    // Here you are adding optios for each Promo
    let promoOptions = {};
    promoOptions[1] = {
        pizzas: [
          tradicionales.arrPizzas[11], // Pepperoni Familiar
          tradicionales.arrPizzas[14], // 4 Estaciones Familiar
          tradicionales.arrPizzas[17], // Americana Familiar
          tradicionales.arrPizzas[20]  // Hawaiana Familiar
        ], 
        bebidas: [
          bebidas.arrBebidas[3], // Inca Kola Original 1.5L
          bebidas.arrBebidas[4], // Inca Kola Zero 1.5L
          bebidas.arrBebidas[7], // Coca Cola Original 1.5L
          bebidas.arrBebidas[8]  // Coca Cola Zero 1.5L
        ], 
        complementos: [
          complementos.arrComplementos[0] // Pan al ajo de 6 soles
        ]
      };
    promoOptions[2] = {
        pizzas: [
          casa.arrPizzas[2], // Zarela Familiar
          casa.arrPizzas[5]  // All Meats Familiar
        ],
        bebidas: [
          bebidas.arrBebidas[3], // Inca Kola Original 1.5L
          bebidas.arrBebidas[4], // Inca Kola Zero 1.5L
          bebidas.arrBebidas[7], // Coca Cola Original 1.5L
          bebidas.arrBebidas[8]  // Coca Cola Zero 1.5L
        ],
        complementos: [
          complementos.arrComplementos[0] // Pan al ajo de 6 soles
        ]
      };
    promoOptions[3] = {
        pizzas: [
          tradicionales.arrPizzas[10], // Pepperoni Grande
          tradicionales.arrPizzas[13], // 4 Estaciones Grande  
          tradicionales.arrPizzas[16]  // Americana Grande
        ],
        bebidas: [
          bebidas.arrBebidas[3], // Inca Kola Original 1.5L
          bebidas.arrBebidas[4], // Inca Kola Zero 1.5L
          bebidas.arrBebidas[7], // Coca Cola Original 1.5L
          bebidas.arrBebidas[8]  // Coca Cola Zero 1.5L
        ],
        complementos: [
          complementos.arrComplementos[3] // Lasagna
        ]
      };
    promoOptions[4] = {
        pizzas: [
          tradicionales.arrPizzas[10], // Pepperoni Grande
          tradicionales.arrPizzas[13], // 4 Estaciones Grande  
          tradicionales.arrPizzas[16], // Americana Grande
          tradicionales.arrPizzas[19]  // Hawuaiana Grande
        ],
        bebidas: [],
        complementos: [],
      };
    promoOptions[5] = {
        pizzas: [
          tradicionales.arrPizzas[10], // Pepperoni Grande
          tradicionales.arrPizzas[13], // 4 Estaciones Grande  
          tradicionales.arrPizzas[16]  // Americana Grande
        ],
        bebidas: [
          bebidas.arrBebidas[9] // Cerveza Cusqueña
        ],
        complementos: [
          complementos.arrComplementos[0] // Pan al ajo de 6 soles
        ],
      };
    

    lengthOfArrPromociones = promos.arrPromociones.length;
    renderPortada = <Portada pizzaTitle={promos.name} />;
    for (let i = 0; i < lengthOfArrPromociones; i ++) {
      renderPromo.push(
        <DisplayPromo
          key = {i}
          i = {i}
          promoName = {promos.arrPromociones[i].name}
          promoOptions = {promoOptions[i+1]}
          promoPrice = {promos.arrPromociones[i].price}
          promoImg = {imgArr[i]}
        />
      );
    }


  } else if (pizzasStatus === 'failed') {

    renderPromo = <section>{error}</section>

  }

  useEffect(() => {
    const arrNumPromos = [];
    for (let i = 1; i <= lengthOfArrPromociones; i++) {
      promosCounter[i].length > 0 && arrNumPromos.push(true);
    }
    if (arrNumPromos.length !== 0) {
      setShowCarrito(
        <div><Link to='/carrito'><button className='botonCarrito'>Ir al carrito</button></Link></div>
      );
    } else {
      setShowCarrito(null);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [promosCounter])

  return (
    <React.Fragment>
      {renderPortada}
      <section className='promociones'>
        {pizzasStatus === 'loading' && renderPromo}
        {pizzasStatus === 'succeeded' && (
          <section className='promocionesInside'>
            {renderPromo}
          </section>
        )}
        {pizzasStatus === 'failed' && renderPromo}
        {showCarrito}
      </section>
    </React.Fragment>
  )
}

export default Promociones
