import { configureStore } from '@reduxjs/toolkit';
import barToogleReducer from './barToogle'
import pizzasReducer from './pizzasSlice';
import counterReducer from './counterSlice';
import infoUserReducer from './infoUserSlice';
import pagoReducer from './pagoSlice';
import answerReducer from './clientAnswerSlice';

export default configureStore({
  reducer: {
    barToogle: barToogleReducer,
    pizzas: pizzasReducer,
    counter: counterReducer,
    infoUser: infoUserReducer,
    payForm: pagoReducer,
    clientAnswer: answerReducer,
  }
});