import API from './client';

// API interface for requesting formulario de pago
export const payForm = async (infoUser) => {
  
  try {

    // "infoUser" is already an object
    const respose = await API.post('payForm', infoUser);
    return respose.data;

  } catch (err) {

    if (err.response) {
      // The request was made and the server responded with a status code
      // that falls out of the range of 2xx
      console.log(err);
      console.log(err.response);
      console.log(err.response.data);
      // console.log(err.response.status);
      // console.log(err.response.headers);
      console.log('before err.response.data')
      throw err.response.data;
    } else if (err.request) {
      // The request was made but no response was received
      // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
      // http.ClientRequest in node.js
      console.log(err.request);
      // console.log('before err.request')
      throw err.request;
    } else {
      // Something happened in setting up the request that triggered an Error
      console.log('Error message', err.message);
    }
    console.log(err.config);

  }
};